import React, { Component } from 'react';
import * as Utils from './../utils.js';
import TaxtaxCalculator from '../lib/TaxCalculator';

import { connect } from 'react-redux'

class ResultView extends Component {
    render() {
        const ResultRow = (props) => {
            const {label, value, id} = props;
            return (
                <div className="row" id={id}>
                    <div className="col-md-6 col-xs-6">{label}</div>
                    <div className="col-md-6 col-xs-6">{value}</div>
                </div>
            )
        };
        
        const { fields } = this.props;

        if (!fields) {
            return (<div/>)
        }

        const taxCalculator = new TaxtaxCalculator(fields.av, fields.rate, fields.USD2MMK, fields.vehicleValueInMMK, fields.slipValue);

        const totalCost = Utils.formatNumber(taxCalculator.grandTotal());
        
        return (
            <div className="col-md-5">
                <h2></h2>
                <ResultRow label="" value={"$"+Utils.formatNumber(taxCalculator.customDuty())}/>
                <ResultRow label="" value={"$"+Utils.formatNumber(taxCalculator.specialGoodsTax())}/>
                <ResultRow label="" value={"$"+Utils.formatNumber(taxCalculator.commercialTax())}/>
                <ResultRow label="" value={"$"+Utils.formatNumber(taxCalculator.incomeTax())}/>
                <ResultRow label="" value={"$"+Utils.formatNumber(taxCalculator.kaNyaNaTax())}/>
                <hr/>
                <ResultRow label="" value={Utils.formatNumber(taxCalculator.totalTaxInMMK()) + " "}/>
                <hr/>
                <ResultRow label="" value={Utils.formatNumber(taxCalculator.preIncomeTax()) + " "}/>
                <hr/>
                <div className="col-md-10">
                    <h3></h3>
                    <label> +  +  +  ()</label>
                    <p className="totalCost text-center">{totalCost} </p>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        fields: state.calculator.fields
    }
};

ResultView = connect(mapStateToProps)(ResultView);

export default ResultView;


